import {baseUrl} from './baseUrl';
import * as ActionTypes from './ActionTypes';

export const fetchData = (fetchType, variables, callback) => {

    fetch( baseUrl, {
        method: 'POST',
        //credentials: 'include',       // if you will turn on this then in django setting you have to allow coros
        headers: {
                    'Content-Type': 'application/json',
                },
        body: JSON.stringify({ query:fetchType, variables:variables })  //JSON.stringify({ query: mutation })
    })
        .then(response => {
            if (response.ok){
                return response
            }
            else { 
                var error = new Error('Error: '+ response.status + ': ' + response.statusText); 
                error.response = response;  
                throw error; 
            }

        },               
        error => {
            var errmess = new Error(error.message); 
            throw errmess;
        })
        .then( data => data.json())
        .then(data => {
            callback(data.data)})
        .catch(error => alert("error: " + error.message + "\n Please refresh your page"));
        
}

 
export function reducer(state, action){
    switch (action.type) {
        case ActionTypes.FETCH_TASK_DATA:
            return {...state, tasks:action.payload}
        
        case ActionTypes.CREATE_TASK:
            const newTask = {"node":action.payload}
            const addingNew = [newTask].concat(state.tasks)
            return {...state, tasks: addingNew}
        
        case ActionTypes.UPDATE_TASK:
            const tasks = state.tasks.filter((taskItem) => {
                if(taskItem.node.id === action.payload.idIs){
                    taskItem.node.title = action.payload.task.title
                    taskItem.node.completed = action.payload.task.completed
                    return taskItem
                }else{
                    return taskItem
                }
            })
            return {...state, tasks:tasks}

        case ActionTypes.DELETE_TASK:
            return {...state, tasks: state.tasks.filter(taskItem => taskItem.node.id  !== action.payload )}

        default:
            return state;
    }
}
