import { fetchData } from "../fetchData/fetchFun";

// this is for fetching the data
export const fetchTaskList = (callback) => {
    const variables = {
        "id": "",
        "title": ""
      }
      
    const query = `
        query {
            allTasks(first:20){
                edges{
                  node{
                    id
                    title
                    completed
                  }
                }
            }
        }
    `;	
  
    fetchData(query, variables, callback);
}

// this is for creating new task
export const fetchCreateTask = (title, callback) => {
  const variables = {
    "title": title
  }

	const mutation = `
    mutation creatingTask( $title: String!){
      createTask(input:{
        title:$title
      }){
      task{
          id
          title
          completed
        }
      }
    }
  `;
  
  fetchData(mutation, variables, callback);
};

// this is for deleting the task
export const fetchDeleteTask = (task, callback) => {
  
  const variables = {
    "id": task.node.id
  }

	const mutation = `
    mutation deleteTask( $id:ID!){
      deleteTask(input:{
        Id:$id
      }){
      task{
          id
        }
      }
    }  
  `;
  
  fetchData(mutation, variables, callback);
};

// this is for doing strike and unstrike
export const fetchupdate = (task, strike, callback) => {

  var variables = {
    "id": task.node.id,
    "title":task.node.title,
  }

  if (strike === true){
    variables = {
      "id": task.node.id,
      "completed":  !task.node.completed  
    }
  }

  const mutation = `
    mutation updateTask( $id:ID! , $title: String, $completed:Boolean){
      updateTask(input:{
        Id:$id, title: $title, completed: $completed
      }){
      task{
          id
          title
          completed
        }
      }
    }  
  `;

  fetchData(mutation, variables, callback);
};