import React, {createContext, useState, useEffect, useReducer} from 'react';
import { fetchTaskList, fetchCreateTask, fetchDeleteTask, fetchupdate } from '../taskActions/fetchTask';
import { reducer } from '../fetchData/fetchFun';
import * as ActionTypes from '../fetchData/ActionTypes';

export const TaskContext = createContext();
    

// this is the default funtion of this file which will called first
function TaskContextProvider(props){
    const [fetchData, setFetchData] = useState(false);
    const [tasks, dispatch] = useReducer(reducer, {tasks:[]});
    let idIs = 0;

    // this will help to fetch the data
    const fetchTasks = (data) => dispatch({
        type: ActionTypes.FETCH_TASK_DATA,
        payload: data.allTasks.edges
    }); 

    // this is for update the state this is the callback method
    const creatingTask = (data) => dispatch({
        type: ActionTypes.CREATE_TASK,
        payload: data.createTask.task
    });

    // this method is called by the user on creating title
    const createTask = (title) => {
        fetchCreateTask(title, creatingTask);
    };

    //  this is the callback method which will update the state
    const deletetingTask = (data) => dispatch({
        type: ActionTypes.DELETE_TASK,
        payload: idIs
    });

    // this is method is used by user 
    const deleteTask = (task) => {
        idIs = task.node.id;
        fetchDeleteTask(task, deletetingTask);
    }
    
    // this method will help to call the reducer and update the state
    const updateTask = (data) => dispatch({
       type: ActionTypes.UPDATE_TASK,
       payload: {"idIs":idIs, "task":data.updateTask.task}  
    });

    // so this method is called by the user 
    const  updatingTask = (task, strike) => {
        idIs = task.node.id        
        fetchupdate(task, strike, updateTask)
    }
    
    useEffect(() => {
        if(fetchData === false){
            fetchTaskList(fetchTasks); // so here we fetching all the data
            setFetchData(true);
        }
    },[]) 

   return (
       <TaskContext.Provider value={{...tasks, createTask: createTask, deleteTask: deleteTask, updatingTask:updatingTask}}>
           {props.children}                              {/*here props.children means we provide all the data to the children*/}   
       </TaskContext.Provider>
   );

}

export default TaskContextProvider;